import Konva from "konva";
import { ScoreData } from "@/model/ScoreData";
import { CustomKeyConfig, DefaultKeyConfig } from "@/model/KeyConfig";
import { CustomKeyKind } from "@/model/KeyKind";
import { freezeColors, noteColors, noteWidth, noteHeight, verticalSizeNum } from "../EditorConstant";
import toPx from "../helper/toPx";
import { cloneDeep } from "lodash";
import { Operation } from "@/model/OperationQueue";

export class NoteService {
  private keyNum: number;

  constructor(
    private scoreData: ScoreData,
    private keyConfig: CustomKeyConfig = DefaultKeyConfig,
    private keyKind: CustomKeyKind = "",
    private isReverse: boolean,
    private stage: Konva.Stage,
    private notesLayer: Konva.Layer,
    private operationStack: Operation[]
  ) {
    this.keyNum = this.keyConfig[this.keyKind].num;
  }

  private isHighlightedFreeze: string = JSON.parse(localStorage.getItem("isHighlightedFreeze") ?? "true");

  private stackPush(operation: Operation): void {
    const maxStackSize = 1000;
    this.operationStack.push(operation);
    if (this.operationStack.length > maxStackSize) this.operationStack.shift();
  }

  // 
  hasNote(page: number, lane: number, position: number): { exists: boolean; isFreeze: boolean } {
    const pageScore = this.scoreData.scores[page - 1];
    const isFreeze = pageScore.freezes[lane].includes(position);
    const exists = pageScore.notes[lane].includes(position) || isFreeze;
    return { exists, isFreeze };
  }

  // 
  add(page: number, lane: number, position: number, isFreeze: boolean): void {
    if (isFreeze) this.scoreData.scores[page - 1].freezes[lane].push(position);
    else this.scoreData.scores[page - 1].notes[lane].push(position);
  }

  // 
  remove(page: number, lane: number, position: number): void {
    const removedFreezes = this.scoreData.scores[page - 1].freezes[lane].filter((pos) => pos !== position);
    this.scoreData.scores[page - 1].freezes.splice(lane, 1, removedFreezes);

    const removedNotes = this.scoreData.scores[page - 1].notes[lane].filter((pos) => pos !== position);
    this.scoreData.scores[page - 1].notes.splice(lane, 1, removedNotes);
  }

  // 
  draw(lane: number, position: number, isFreeze: boolean, orgLane: number = lane): void {
    const stage = this.stage as Konva.Stage;
    const notesLayer = this.notesLayer as Konva.Layer;

    const colorGroup = this.keyConfig[this.keyKind].colorGroup;
    const color = isFreeze ? freezeColors[colorGroup[lane]] : noteColors[colorGroup[lane]];

    const note = new Konva.Rect({
      x: orgLane * noteWidth,
      y: toPx(position, this.isReverse) - noteHeight / 2,
      width: noteWidth,
      height: noteHeight,
      fill: color,
      id: `note-${orgLane}-${position}`,
    });
    notesLayer.add(note);
    stage.add(notesLayer);
  }

  // 
  clear(lane: number, position: number): void {
    const stage = this.stage;
    const notesLayer = this.notesLayer;

    const note = notesLayer.findOne(`#note-${lane}-${position}`);
    note?.destroy();
    stage.add(notesLayer);
  }

  // 1
  addOne(
    page: number,
    displayPage: number,
    lane: number,
    position: number,
    isFreeze: boolean,
    orgLane: number = lane,
    pageBlockNum: number
  ) {
    const frzList = [];
    this.scoreData.scores.forEach((score) => frzList.push(...score.freezes[lane]));

    // 
    const isFreezeFlg = isFreeze || frzList.length % 2 !== 0;

    this.add(page, lane, position, isFreezeFlg);
    if (page === displayPage) {
      this.draw(lane, position, isFreezeFlg, orgLane);
    }
    if (isFreezeFlg) this.fillFreeze(displayPage, lane, orgLane, pageBlockNum);
    this.stackPush({
      type: "ADD_NOTE",
      page,
      position,
      lane,
      isFreeze,
    });
  }

  // 1
  removeOne(page: number, displayPage: number, lane: number, position: number, orgLane: number = lane, pageBlockNum: number) {
    const { isFreeze } = this.hasNote(page, lane, position);

    this.remove(page, lane, position);
    if (page === displayPage) {
      this.clear(orgLane, position);
    }
    this.fillFreeze(displayPage, lane, orgLane, pageBlockNum);
    this.stackPush({
      type: "REMOVE_NOTE",
      page,
      position,
      lane,
      isFreeze,
    });
  }

  // 
  switchOne(
    page: number,
    displayPage: number,
    lane: number,
    position: number,
    isFreeze: boolean,
    orgLane: number = lane,
    pageBlockNum: number
  ) {
    if (this.hasNote(page, lane, position).exists) {
      this.removeOne(page, displayPage, lane, position, orgLane, pageBlockNum);
    } else {
      this.addOne(page, displayPage, lane, position, isFreeze, orgLane, pageBlockNum);
    }
  }

  // 
  fillFreeze(page: number, lane: number, orgLane: number = lane, pageBlockNum: number) {
    if (!this.isHighlightedFreeze) return false;
    const stage = this.stage;
    const notesLayer = this.notesLayer;

    const colorGroup = this.keyConfig[this.keyKind].colorGroup;
    const color = freezeColors[colorGroup[lane]];

    const laneFreezes: number[] = cloneDeep(this.scoreData.scores[page - 1].freezes[lane]).sort((a, b) => a - b);

    const startParity =
      this.scoreData.scores.reduce((acc, score, index) => {
        return index < page - 1 ? acc + score.freezes[lane].length : acc;
      }, 0) % 2;

    if (startParity === 1) laneFreezes.unshift(0);
    laneFreezes.push(verticalSizeNum(pageBlockNum));

    const freezeClass = `.freeze-fill-${orgLane}`;
    const fills = notesLayer.find(freezeClass);
    fills.forEach((node) => node.destroy());

    while (laneFreezes.length > 0) {
      const freezeStart = laneFreezes.shift() as number;
      const freezeEnd = laneFreezes.shift() ?? verticalSizeNum(pageBlockNum);
      const height = freezeEnd - freezeStart;
      const opacity = 0.3;

      const fillFreeze = new Konva.Rect({
        x: orgLane * noteWidth,
        y: Math.min(toPx(freezeStart, this.isReverse), toPx(freezeEnd, this.isReverse)),
        width: noteWidth,
        height,
        opacity,
        fill: color,
        name: `freeze-fill-${orgLane}`,
        id: `freeze-fill-${orgLane}-${freezeStart}`,
      });
      notesLayer.add(fillFreeze);
    }
    stage.add(notesLayer);
  }

  // 
  removeLanes(page: number, position: number): NoteOnPosition[] {
    const keyNum = this.keyNum;
    const removedLanes: NoteOnPosition[] = [];
    for (let lane = 0; lane < keyNum; lane++) {
      const noteStatus = this.hasNote(page, lane, position);
      if (noteStatus.exists) {
        this.remove(page, lane, position);
        removedLanes.push({
          lane,
          isFreeze: noteStatus.isFreeze,
        });
      }
    }
    return removedLanes;
  }

  removeOnPosition(page: number, position: number): void {
    const removedLanes = this.removeLanes(page, position);
    this.stackPush({
      type: "REMOVE_ON_POSITION",
      page,
      position,
      removedLanes,
    });
  }

  // 
  addOnPosition(page: number, currentPosition: number, notesOnPosition: NoteOnPosition[]) {
    notesOnPosition.forEach((obj) => this.add(page, obj.lane, currentPosition, obj.isFreeze));
  }

  // 
  shift(page: number, currentPosition: number, delta: number, pageBlockNum: number): void {
    let newPosition = currentPosition + delta;
    let newPage = page;
    if (newPosition < 0) {
      if (page === 1) return;
      else {
        newPage--;
        newPosition += verticalSizeNum(pageBlockNum);
      }
    } else if (newPosition >= verticalSizeNum(pageBlockNum)) {
      newPage++;
      newPosition -= verticalSizeNum(pageBlockNum);
    }

    const movedLanes = this.removeLanes(page, currentPosition);
    const originalLanes = this.removeLanes(newPage, newPosition);
    this.addOnPosition(newPage, newPosition, movedLanes);

    this.stackPush({
      type: "SHIFT",
      page,
      position: currentPosition,
      newPage,
      newPosition,
      movedLanes,
      originalLanes,
    });
  }
}

export type NoteOnPosition = {
  lane: number;
  isFreeze: boolean;
};
